
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Group,
Cell,
XTextarea,
Toast
},
data () {
return {
confrim:"",
toast:false,
toasts:false,
/*  */
tpl:{},
orderLogistics:'/orderLogistics/',
/*  */
haveDefault:false,
/* id */
cartId:0,
/*  */
infoData:{},
/*  */
timeMap:{1:"",2:"",3:"",4:"",5:""},
/*  */
tplRules:{},
/*  */
option:"",
paydata:{

},
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/*  */
returnTpl(){
this.getTplPrice();
if(this.$store.state.tplId==1){
return "";
}else if(this.$store.state.tplId==2){
return "";
}else if(this.$store.state.tplId==3){
this.tpl=this.$store.state.sinceData;
return "";
}else{
return "";
}
},
/*  */
rentRange(){
let self=this;
let start=dateFormat(new Date(self.infoData.cart_start_time*1000), 'YYYY-MM-DD');
let end=dateFormat(new Date(self.infoData.cart_end_time*1000), 'YYYY-MM-DD');
return {start,end};
},
/*  */
/*  */
timeText(){
return this.timeMap[this.infoData.rent_period_type];
},
/*  */
goodsDespoit(){
if(this.infoData.goods_deposit==0){
return 0;
}
let despoite=this.infoData.goods_deposit*this.infoData.cart_content_good_amount-this.goodsAllPrice;
if(despoite<=0){
return 0;
}else{
return despoite;
}
},
/*  */
/* **++ */
goodsAllPrice(){
return (this.infoData.rent_period_now_rent_price*this.infoData.cart_time_number*this.infoData.cart_content_good_amount);
},
/*  */
returnTplPrice(){
let num=this.infoData.cart_content_good_amount;
let second=num-1;
if(this.$store.state.tplId==1){
return  this.tplRules.freight_first_price+this.tplRules.freight_continued_price*second-0;
}else if(this.$store.state.tplId==2){
return this.tplRules.freight_door_cost*num-0;
}else if(this.$store.state.tplId==3){
this.tpl=this.$store.state.sinceData;
return 0;
}else{
return 0;
}
}
},
methods:{
/*  */
onBridgeReady(){
let self=this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest',self.paydata,
function(res){
if(res.err_msg =="get_brand_wcpay_request:fail")  {
self.confrim="";
self.toast=true;
location.href="/#/index/main/order";

}
if(res.err_msg =="get_brand_wcpay_request:cancel")  {
location.href="/#/index/main/order";
}
if(res.err_msg == "get_brand_wcpay_request:ok" ) {
self.confrim="";
self.toast=true;
location.href="/#/index/main/order";
}     // ,res.err_msg    ok
}
);
},
routerBack(){
this.$router.goBack();
},
/*  */
buygoods(){
/*  */
if(this.getAddress==""){
this.confrim="";
this.toasts=true;
return false;
}
if(this.returnTpl==""){
this.confrim="";
this.toasts=true;
return false;
}

API.order.orderPay({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
info:[{
addressId:this.$store.state.tplId !=3 ? this.$store.state.addressData.id:"",//id 
cartId: this.cartId, //id
cart_tpl:this.$store.state.tplId,//
freight_continued_price: this.tplRules.freight_continued_price,//
freight_door_cost: this.tplRules.freight_door_cost,//
freight_first_price:this.tplRules.freight_first_price,//
goodsAmount: this.infoData.cart_content_good_amount,//
order_time_number: this.infoData.cart_time_number,//
rentTime: this.infoData.cart_start_time,                    rent_period_id: this.infoData.cart_content_rent_prieod_id,                    returnTime: this.infoData.cart_end_time,                    sinceId: this.$store.state.tplId==3?this.$store.state.sinceData.since_id:""//id
}]
}).then((res)=>{
if(res.body.code==200){
let self=this;
let openId=localStorage.getItem("openId");
API.order.OrderWechat({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderSn:res.body.data.order_big_sn,
payMethod:3,
openId:openId,
}).then((resopndy)=>{
this.paydata=resopndy.body;
console.log(resopndy.body);
if (typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
}else{
self.onBridgeReady();
}
},(err)=>{
alert(JSON.stringify(err));
});
}
});
},
/*  */
Initialization(){
this.cartId=this.$route.params.id;
API.order.orderConfirm({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
cartId:[this.cartId],
}).then((res)=>{
this.infoData=res.body.data.cart_list[0];
this.orderLogistics="/orderLogistics/"+this.infoData.cart_content_goods_id;
});
this.getDefaultAddress();
/*  */
this.$store.dispatch('ClearTpl');
},
/* vuex */
getDefaultAddress(){
API.person.addressInfo({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
is_default:1
}).then((res)=>{
if(res.body.code==200){
this.haveDefault=true;
res= res.body.data.addressList;
this.$store.dispatch('SetAddress',res);
}else{
this.haveDefault=false;
}
});
},
/*  */
getTplPrice(){
if(this.$store.state.addressData.detail!=""){
API.order.tplPrice({
tpl:this.$store.state.tplId,
province:this.$store.state.addressData.province,
city:this.$store.state.addressData.city,
region:this.$store.state.addressData.district,
goodsId:this.infoData.cart_content_goods_id
}).then((res)=>{
if(res.body.code==200){
this.tplRules=res.body.data;

}
});
}
}
},mounted(){
/*  */
this.Initialization();
},
activated(){
/*  */
if(this.$store.state.currentOrder!=this.cartId){
this.Initialization();
}
}
}



