import {API,getQuery} from './services/';


export default {
    data() {
return {
transitionName: 'slide-right'  // slide-right
}
},
watch: {
'$route' (to, from) {
let isBack = this.$router.isBack  //  
if(isBack) {
this.transitionName = 'slide-right'
} else {
       this.transitionName = 'slide-left'
}
this.$router.isBack = false
}
  },
    mounted(){

        
            
function getQueryString(name) {  
	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");  
	var r = location.search.substr(1).match(reg);  
	if (r != null)  
		return unescape(decodeURI(r[2]));  
	return null;  
} 
let openId=localStorage.getItem("openId");

var access_code = getQueryString('code'); 

if(!openId){
    if(access_code == null ){
	 var fromurl = location.href;//codecode  
	 var url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxe67019703f582d76&redirect_uri=' + encodeURIComponent("http://wap.zujiekeji.cn") + '&response_type=code&scope=snsapi_base&state=0#wechat_redirect';  
	location.href = url;
    }else{
        if(!openId){
            API.order.getOpenId({
                code:access_code,
            }).then((res)=>{
                let openid=res.body.data;
                localStorage.setItem("openId",openid);
            },(res)=>{
            });
        }
        
    }
}
    }
  }



